.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 88vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ticket {
	background-color: darkslateblue;
	padding: 8px;
	margin: 5px;
	width: 630px;
}

.title {
	font-size: 14pt;
}

.subtasks {
	font-size: 9pt;
}

.subtasksBlock {
	float: left;
	text-align: left;
	padding-left: 10px;
	padding-right: 10px;
}

.clearit {
	clear: both;
}

.timelog {
	font-size: 13pt;
}


